@import url('/static/museum/css/default.css');

@import 'base/util'; //mixins
@import '../../commons/scss/breakpoint'; //반응형 플러그인
@import 'base/variables'; //환경 설정
@import 'base/color'; // colors
@import 'components/sprite'; //아이콘 스프라이트
[class^='ico-'] {
	display: inline-block;
}


/* 레이아웃 */
#wrap {
	padding-top: 0;
}
.row {
	@include flex-row;
	align-items: flex-start;
}
#content.main-container {
	margin-top: 0;
}

@include breakpoint($lg){

	#section2 section,
	#section3 section,
	#section4 section,
	#section5 section,
	#section6 section {
		padding-top: calc((100vh - (1080px + 194px)) / 2 + 194px);
		//padding-top: calc((100vh - (778px + 75px)) / 2 + 75px);
	}
}

/* footer */
#footer {
	margin: 0;
}

/* section, 공통 */
.section {
	// min-height: 1080px;
	overflow: hidden;
	@include breakpoint($sm) {
		min-height: initial;
		height: auto;
	}
	h2 {
		color: #131547;
		font-size: rem(48);
		font-weight: 700;
		margin-bottom: 90px;
	}
	section {
		width: 1680px;
		max-width: 100%;
		margin: 0 auto;
		// border: 1px solid red;
		// box-sizing: border-box;
	}
	.b-more {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		width: 36px;
		height: 36px;;
		@include background-image-retina("/static/museum/img/ico/ico-more", png, 100%, 100%);
	} 
}

/* section1 비주얼 */
#section1 {
	position: relative;
	height: 100vh;
}
.section1-visual {
	width: 100vw;
	height: 100vh;
	@include breakpoint($sm) {
		height: auto;
	}
	.swiper-slide {
		position: relative;
		height: 100vh;
		align-items: center;
		background-size: cover;
		background-attachment: fixed;
		background-position: center center;
		@include breakpoint($sm) {
			min-height: 760px;
			height: auto;
		}
		&::before {
			content: "";
			display: inline-block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			// background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 82.44%, rgba(0, 0, 0, 0.60) 94.29%);
			background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2) 100%)
		}
		.s1-txt-set {
			position: relative;
			z-index: 2;
			width: 100%;
			max-width: 1680px;
			padding: 0 20px;
			margin: 160px auto 0;
			box-sizing: border-box;
			@include breakpoint($xl) {
				padding: 0 50px;
			}
			@include breakpoint($sm) {
				margin: -300px auto 0;
			}
			br {
				@include breakpoint($sm) {
					display: none;
				}
			}
		}
		.s1-t1 {
			color: #fff;
			font-size: rem(44);
			font-style: normal;
			font-weight: 700;
			line-height: 110%;
			text-transform: uppercase;
			text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
			@include breakpoint($sm) {
				font-size: rem(32);
			}
		}
		.s1-t2 {
			margin-top: 24px;
			color: #fff;
			font-size: rem(20);
			font-style: normal;
			font-weight: 400;
			line-height: normal;
			text-transform: uppercase;
			text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
			@include breakpoint($sm) {
				font-size: rem(18);
			}
		}
	}
	&__ctrl {
		position: absolute;
		top: 68%;
		left: 50%;
		width: 100%;
		padding: 0 20px;
		max-width: 1680px;
		box-sizing: border-box;
		transform: translateX(-50%);
		z-index: 100;
		display: flex;
		flex-direction: row;
		align-items: center;
		@include breakpoint($xl) {
			padding: 0 50px;
		}
		@include breakpoint($sm) {
			top: 44%;		
		}
		

		.section1-visual__swiper-button-prev {
			margin-top: -8px;
			font-size: 24px;
			&::before {
				@include font-ico-outline('\e314');
				color: #fff;
			}
		}
		.section1-visual__swiper-button-next {
			margin-top: -8px;
			font-size: 24px;
			&::before {
				@include font-ico-outline('\e315');
				color: #fff;
			}
		}
		.section1-visual__swiper-button-stop {
			font-size: 24px;
			margin-top: -8px;
			transform: translateX(8px);
			&::before {
				@include font-ico-outline('\e034');
				color: #fff;
			}
		}
		.section1-visual__swiper-button-play {
			display: none;
			font-size: 24px;
			margin-top: -8px;
			transform: translateX(8px);
			&::before {
				@include font-ico-outline("\e037");
				color: #fff;
			}
		}
		.section1-visual__swiper-button-prev,
		.section1-visual__swiper-button-next {
			&.swiper-button-disabled {
				opacity: .4;
			}
		}
	}
	.swiper-pagination {
		position: static;
		color: rgba(255, 255, 255, 0.6);
		font-weight: 300;
		&-current {
			font-size: rem(20);
			font-weight: 700;
			color: rgba(255, 255, 255, 1);
		}
		&-total {
			font-size: rem(18);
			margin-left: 82px;
		}
		span {
			display: inline-block;
			padding: 0 16px;
		}
	}
	.autoplay-progress {
		position: absolute;
		top: 50%;
		left: calc(78px + 50px);
		width: 82px;
		height: 4px;
		transform: translateY(-50%);
		background: rgba(255,255,255,0.6);
		@include breakpoint($sm) { 
			left: calc(78px + 20px);
		}
		.progress {
			display: block;
			width: 0px;
			height: 4px;
			background: #39A0FF;
			&.active {
				animation: 6s progress linear;
			}
			&.first {
				animation: 5s progress linear;
			}

		}
		
	}
}
.section1-bottom {
	position: absolute;
	left: 50%;
	bottom: 0;
	padding: 16px 0;
	width: 100%;
	max-width: 1680px;
	padding: 16px 50px;
	transform: translateX(-50%);
	box-sizing: border-box;
	border-top: 1px solid rgba(255, 255, 255, 0.30);
	box-sizing: border-box;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	z-index: 2;
	gap: 20px 0;
	@include breakpoint($sm) {
		padding: 16px 20px;
	}
	.museum-info {
		gap: 0 36px;
		@include breakpoint($sm) {
			gap: 10px 0;
			flex-direction: column;
		}
		&__date {
			font-size: rem(28);
			font-weight: 500;
			color: #fff;
			span {
				display: block;
				font-size: rem(16);	
				font-weight: 400;
			}
		}
		&__holiday {
			font-size: 14px;
			font-weight: 300;
			color: #fff;
			span {
				display: block;
				font-weight: 400;
				font-size: rem(16);	
				margin-bottom: 4px;
				i {
					margin-right: 4px;
				}
			}
		}
	}
	.museum-reserve {
		gap: 0 12px;
		@include breakpoint($smd) {
			flex-wrap: wrap;
			gap: 6px 10px;
		}
		i {
			display: inline-block;
			width: 32px;
			height: 32px;
			margin-right: 4px; 
		}
		a {
			display: flex;
			width: 164px;
			height: 48px;
			padding: 8px 12px;
			align-items: center;
			justify-content: center;
			font-size: 14px;
			font-weight: 500;
			color: #fff;
			border-radius: 99px;
			background: rgba(255,255,225,0.1);
			&:hover,
			&:focus {
				background: #005596;
			}
			@include breakpoint($md) {
				background: rgba(255,255,225,0.1);
			}
			@include breakpoint($sm) {
				width: 132px;
			}
		}
		
	}

}

/* section2 게시판 */
#section2 {
	padding: 180px 0 200px;
	box-sizing: border-box;
	background: #F2F6FA;
	@include breakpoint($smd) {
		padding: 80px 0 0;
		height: auto;
	}
	section {
		position: relative;
		> .row {
			justify-content: space-between;
			padding: 0 20px;
			@include breakpoint($xl) {
				padding: 0 50px;
			}
			@include breakpoint($smd) {
				flex-direction: column;
			}
		}
	}
	h2 {
		width: 100%;
		margin: 0 auto 64px;
		padding: 0 20px;
		box-sizing: border-box;
		@include breakpoint($xl) {
			padding: 0 50px;
		}
		@include breakpoint($sm) {
			margin: 0 auto 40px;
		}
	}
	h3 {
		margin-bottom: 24px;
		font-size: rem(28);
		font-weight: 500;
		color: #1A1A1A;
		// @include breakpoint($sm) {
		// 	margin-bottom: 30px;
		// }
	}
}
.section2-news {
	position: relative;
	width: 480px;
	margin-right: 7.14286%;
	background: #F2F6FA;
	z-index: 2;
	@include breakpoint($smd) {
		width: 100%;
		margin-left: 0;
	}
	ul {
		border-top: 2px solid #1A1A1A;
		li {
			padding: 14px 0;
			border-bottom: 1px solid #E5E5E5;
			box-sizing: border-box;
			span {
				display: block;
			}
			.tit {
				font-size: rem(18);
				font-weight: 500;
				color: #222;
			}
			.desc {
				display: -webkit-box;
				max-height: 48px;
				margin: 12px 0;
				font-size: rem(16);
				font-weight: 400;
				overflow: hidden;
				text-overflow: ellipsis;
				-webkit-line-clamp: 2; 
				-webkit-box-orient: vertical;
				color: #4D4D4D;
			}
			.date {
				font-size: rem(14);
				font-weight: 400;
				color: #666;
			}
		}
	}
}
.section2-exhibit {
	position: relative;
	width: calc(100% - 480px - 7.14286%);
	@include breakpoint($smd) {
		width: 100%;
		margin-top: 100px;
		margin-bottom: 80px;
	}
	@include breakpoint($sm) {
		margin-top: 80px;
	}
	.b-more {
		// right: -50px;
		// @include breakpoint($smd) {
		// 	right: 0;
		// }
	}
	> .row {
		position: relative;
		height: 386px;
		background: #005596;
		border-radius: 16px 0 0 16px;
		@include breakpoint($xl) {
			height: 480px;
		}
		@include breakpoint($sm) { 
			flex-direction: column;
			height: auto;
			border-radius: 16px;
			background: none;
		}
		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100%;
			right: 0;
			z-index: 1;
			background: #0D4E80;
			border-radius: 16px 0 0 16px;
			@include breakpoint($sm) {
				// display: none;
				top: 10px;
				left: -20px;
				border-radius: 0;
			}
		}
	}
	.section2-txt {
		// width: 55.55%;;
		width: calc(100% - 264px - 40px);
		padding: 30px 3% 33px 6%;
		// overflow: visible;
		margin: 0;
		z-index: 2;
		background: #0D4E80;
		border-radius: 16px 0 0 16px;
		@include breakpoint($xl) {
			width: calc(100% - 330px - 120px);
			padding: 72px 3% 84px 11.11%;
		}
		@include breakpoint($smd) {
			width: calc(100% - 264px - 40px - 100px);
		}
		@include breakpoint($sm) {
			order: 2;
			width: 100%;
			margin-top: 80px;
			padding: 0 0 50px;
		}
		.swiper-wrapper {
			height: auto;
		}
		.swiper-slide {
			width: 480px;
			@include breakpoint($sm) {
				text-align: center;
				width: 240px;
			}
			> * {
				opacity: 0;
			}
			> a {
				display: block;
			}
			&.swiper-slide-active {
				> * {
					opacity: 1;
				}
			}
			.current {
				display: inline-block;
				padding: 4px 12px;
				margin-bottom: 8px;
				border-radius: 99px;
				line-height: 1;
				background: #fff;
				color: #3B63C4;
			}
			p {
				font-size: rem(24);
				font-weight: 500;
				color: #fff;
				margin-bottom: 32px;
				@include breakpoint($sm) {
					margin-bottom: 20px;
				}
			}
			ul {
				@include breakpoint($sm) {
					display: flex;
					align-items: center;
					flex-direction: column;
				}
			}
			li {
				position: relative;
				font-size: rem(16);
				font-weight: 300;
				padding-left: 32px;
				color: #fff;
				@include breakpoint($sm) {
					display: inline-block;
				}
				&:first-of-type {
					margin-bottom: 8px;
					@include breakpoint($sm) {
						margin-bottom: 4px;
					}
				}
				i {
					position: absolute;
					top: 0;
					left: 0;
					margin-right: 8px;
				}
			}
		}
		.pagination-top {
			display: flex;
			align-items: center;
			// transform: translateY(-114px);
			margin-top: -114px;
			margin-bottom: 72px;
			margin-right: 15px;
			@include breakpoint ($sm) {
				position: absolute;
				top: -470px;
				right: 0;
				margin-top: 0;
				margin-right: 0;
				margin-bottom: 0;
			}
			.pagination-fraction {
				display: flex;
				align-items: center;
				justify-content: flex-end;
				@include breakpoint($sm) {
					color: #fff;
					line-height: 1;
				}
				span {
					padding: 0 8px;
					font-size: rem(18);
					font-weight: 300;
					color: #1A1A1A;
					@include breakpoint($sm) {
						color: #fff;
						font-size: rem(16);
					}
					&.swiper-pagination-current {
						font-size: rem(28);
						font-weight: 500;
						color: #39A0FF;
						@include breakpoint($sm) {
							font-size: rem(20);
						}
					}
				}
			}
			.section2-stop {
				width: 24px;
				height: 25px;
				@include background-image-retina("/static/museum/img/ico/ico-pauseBk", png, auto, 100%);
			}
			.section2-play {
				width: 24px;
				height: 25px;
				@include background-image-retina("/static/museum/img/ico/ico-playBk", png, auto, 100%);
			}
		}
		.pagination-btm {
			margin-top: 64px;
			@include breakpoint($sm) {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-top: 16px;
				text-align: center;
			}
			button {
				margin: 0 5px;
				@include breakpoint($sm) {
					transform: scale(.8);
				}
				&.swiper-button-disabled {
					opacity: 0.4;
				}
				&.section2-prev {
					&:hover,
					&:focus {
						> i {
							background-position: 0 -136px;
						}
					}
				}
				&.section2-next {
					&:hover,
					&:focus {
						> i {
							background-position: -136px -136px;
						}
					}
				}
			}
		}
	}
	.section2-img {
		// width: calc(100% - 55.55%);
		// width: 33.45%;
		width: 264px;
		margin: 72px 0 0;
		overflow: visible;
		@include breakpoint($xl) {
			width: 330px;
		}
		@include breakpoint($sm) {
			order: 1;
			width: 100%;
			margin: 60px 0 0;
		}
		.swiper-slide {
			// width: 330px;
			// height: 440px;
			aspect-ratio: 330/440;
			transform: scale(0.6363);
			transform-origin: top right;
			opacity: 0;
			@include breakpoint($smd) {
				width: 264px;
				height: 352px;
				transform: scale(0.7);
			}
			@include breakpoint($sm) {
				transform: scale(1);
			}
			&.swiper-slide-visible{
				transition: opacity 0.6s, transform 0.2s;
				opacity: 0.6;
				// & + .swiper-slide:not(.swiper-slide-visible) {
				// 	margin-left: -60px;
				// }
				&.swiper-slide-active {
					opacity: 1;
				}	
			}
			&.swiper-slide-prev{
				transition: opacity 0.6s, transform 0.2s;
			}
			&.swiper-slide-next {
				opacity: 0.6;
				& + .swiper-slide, 
				& + .swiper-slide {
					opacity: 0.6;
				}
			}
			&.swiper-slide-active {
				transform: scale(1) translateY(-100px);
				box-sizing: border-box;
				opacity: 1;
				@include breakpoint($xl) {
					transform: scale(1) translateY(-140px);
				}
				@include breakpoint($sm) {
					transform: scale(1) translateY(0);
				}
				.pdf-down {
					display: block;
				}
			}
			.slide-img {
				width: 100%;
				//height: 100%;
				aspect-ratio: 330/440;
				background-color: #fff;
				background-position: center center;
				background-repeat: no-repeat;
				// background-size: contain;
				background-size: cover;
				box-shadow: 0px 16.635px 19.962px 0px rgba(0, 0, 0, 0.15);
			}
			.pdf-down {
				margin-top: 8px;
				text-align: right;
				display: none;
				a {
					position: relative;
					font-size: rem(16);
					font-weight: 300;
					color: #fff;
					i {
						margin-right: 8px;
					}
					&::before {
						content: "";
						display: inline-block;
						position: absolute;
						width: 100%;
						height: 2px;
						bottom: -8px;
						background: #fff;
					}
				}
			}
		}
	}
}




